.header-logo__title,
.header-logo__description {
  text-shadow: 0 0 10px #000000, 0 0 30px #000000;
}

.navbar-inverse {
  background-color: #181818;
  border-color: transparent;
}

.navbar-inverse .navbar-nav > .active > a {
  background-color: #053259;
}

.navbar-inverse .navbar-nav > .active > a:hover,
.navbar-inverse .navbar-nav > .active > a:focus {
  background-color: #5182ae;
}

.navbar-inverse .navbar-nav > li > a {
  border-radius: 4px;
  transition: background-color 0.5s;
}

.navbar-inverse .navbar-nav > li > a:hover,
.navbar-inverse .navbar-nav > li > a:focus {
  background-color: #165690;
}

.navbar-inverse .navbar-nav.navbar-left > li {
  margin: 5px 5px 5px 0;
}

.navbar-inverse .navbar-nav.navbar-right > li {
  margin: 5px 0 5px 5px;
}

.panel-info {
  border: none;
  margin-bottom: 30px;
  background-color: #f1f1f1;
  overflow: hidden;
}

.panel-info > .panel-heading {
  color: white;
  background-color: #df8a13;
  border: none;
}

.panel-info > .panel-heading h3 {
  letter-spacing: 0.1em;
  font-size: 1.2rem;
}

.panel-info > .panel-body {
  border: 1px solid #dddddd;
  border-radius: 0 0 4px 4px;
}

.carousel-inner > .item img {
  width: 100%;
}

.input-group-lg > .form-control {
  font-size: 0.875rem;
}

.input-lg {
  font-size: 0.875rem;
}

a.thumbnail:hover {
  border-color: transparent;
}

html {
  font-size: 16px;
}

body {
  font-size: 1rem;
  font-family: "PT Sans", sans-serif;
}

a:hover,
a:focus {
  text-decoration: none;
  transition: color 0.5s;
  color: #df8a13;
}

.header-logo {
  padding: 100px 20px;
  text-align: center;
  background-color: #222222;
  background-image: url("../img/header-bg.jpg");
  background-position: 50% 50%;
  background-size: cover;
}

.header-logo__title {
  display: block;
  color: #df8a13;
  font-family: "PT Serif", serif;
  font-size: 3.5rem;
  letter-spacing: 0.33em;
}

.header-logo__description {
  display: block;
  color: white;
  letter-spacing: 0.2em;
  font-size: 1.15rem;
}

.left-sidebar {
  margin-top: 40px;
}

.site-news-item {
  display: grid;
  grid-column-gap: 10px;
  grid-template-areas: "img ttl" "img dte";
  font-size: 1rem;
  line-height: 1.25em;
}

.site-news-item__image {
  grid-area: img;
  width: 75px;
}

.site-news-item__title {
  grid-area: ttl;
}

.site-news-item__date {
  grid-area: dte;
  font-size: 0.8rem;
  color: #aaaaaa;
  text-align: right;
}

.new-films h1 {
  font-family: "PT Serif", serif;
  font-size: 3rem;
  margin: 40px 0;
  position: relative;
  overflow: hidden;
}

.new-films h1::after {
  content: "";
  margin-left: 15px;
  bottom: 8px;
  position: absolute;
  height: 3px;
  width: 100%;
  background-color: #f0ad4e;
}

.new-films .row {
  margin-right: -7.5px;
  margin-left: -7.5px;
}

.new-films-item {
  padding-left: 7.5px;
  padding-right: 7.5px;
}

.new-films-item__image {
  width: 100%;
}

.new-films-item__title {
  font-size: 1rem;
  margin: 0;
  font-weight: 700;
  text-align: center;
  transition: color 0.5s;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.new-films-item .thumbnail {
  transition: background-color 0.5s;
}

.new-films-item:hover .thumbnail {
  background-color: #df8a13;
}

.new-films-item:hover h3 {
  color: #f4f4f4;
}