.left-sidebar {
  margin-top: 40px;
}

.site-news-item {
  display: grid;
  grid-column-gap: 10px;
  grid-template-areas: "img ttl"
                       "img dte";
  font-size: 1rem;
  line-height: 1.25em;

  &__image {
    grid-area: img;
    width: 75px;
  }

  &__title {
    grid-area: ttl;
  }

  &__date {
    grid-area: dte;
    font-size: 0.8rem;
    color: darken($gray-color, 20%);
    text-align: right;
  }
}

.new-films {
  h1 {
    font-family: $font-header;
    font-size: 3rem;
    margin: 40px 0;
    position: relative;
    overflow: hidden;

    &::after {
      content: "";
      margin-left: 15px;

      bottom: 8px;
      position: absolute;
      height: 3px;
      width: 100%;
      background-color: $accent-light;
    }
  }

  .row {
      margin-right: -7.5px;
      margin-left: -7.5px;
  }
}

.new-films-item {
  padding-left: 7.5px;
  padding-right: 7.5px;

  &__image {
    width: 100%;
  }

  &__title {
    font-size: 1rem;
    margin: 0;
    font-weight: 700;
    text-align: center;
    transition: color 0.5s;

    @include text-shortener;
  }

  .thumbnail {
    transition: background-color 0.5s;
  }

  &:hover {
    .thumbnail {
      background-color: $accent-color;
    }

    h3 {
      color: lighten($gray-color, 9%);
    }
  }
}
